<template>
  <div>
    <video ref="video" width="640" height="480" autoplay></video>
    <canvas ref="canvas" width="640" height="480"></canvas>
    <button @click="startRecognition">开始识别</button>
    <button @click="stopRecognition">停止识别</button>
    <button @click="sendData">发送数据</button>
  </div>
</template>

<script>
import * as faceapi from 'face-api.js';
import axios from 'axios';

export default {
  data() 
  {
    return {
      stream: null,
      interval: null,
      detectedFace: null,
    };
  },
  async mounted() {
    await this.loadModels();
    await this.initVideo();
  },
  methods: {
    async loadModels() {
      await faceapi.nets.ssdMobilenetv1.loadFromUri('/models');
      await faceapi.nets.faceLandmark68Net.loadFromUri('/models');
      await faceapi.nets.faceRecognitionNet.loadFromUri('/models');
    },
    async initVideo() {
      const video = this.$refs.video;
      this.stream = await navigator.mediaDevices.getUserMedia({ video: true });
      video.srcObject = this.stream;
    },
    startRecognition() {
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;
      const context = canvas.getContext('2d');

      this.interval = setInterval(async () => {
        const detections = await faceapi.detectAllFaces(video).withFaceLandmarks().withFaceDescriptors();
        context.clearRect(0, 0, canvas.width, canvas.height);

        if (detections.length > 0) {
          const resizedDetections = faceapi.resizeResults(detections, { width: 640, height: 480 });
          faceapi.draw.drawDetections(canvas, resizedDetections);
          faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);

          this.detectedFace = resizedDetections[0].descriptor;
        }
      }, 100);
    },
    stopRecognition() {
      clearInterval(this.interval);
      if (this.stream) {
        this.stream.getTracks().forEach(track => track.stop());
      }
    },
    sendData() {
      if (this.detectedFace) {
        const data = Array.from(this.detectedFace);

        axios.post('http://localhost:4000/face-data', { data })
          .then((response) => {
            console.log('Data sent successfully:', response.data);
          })
          .catch((error) => {
            console.error('Error sending data:', error);
          });
      } else {
        console.warn('No face detected to send.');
      }
    },
  },
  beforeDestroy() {
    this.stopRecognition();
  },
};
</script>

<style scoped>
video, canvas {
  border: 1px solid #ccc;
}
</style>